import { graphql, useStaticQuery } from "gatsby";

type SiteMetadata = {
  title: string;
  siteUrl: string;
};

const useSiteMetadata = (): SiteMetadata => {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title
          siteUrl
        }
      }
    }
  `);

  return data.site.siteMetadata;
};

export default useSiteMetadata;
