import React from "react";
import { Link, useI18next } from "gatsby-plugin-react-i18next";
import { NavDropdown } from "react-bootstrap";

type LanguageProps = {
  className: string;
};

const LanguageSwitcher: React.FC<LanguageProps> = ({ className }) => {
  const { t, originalPath } = useI18next();

  return (
    <NavDropdown className={className} title={t("languageSwitcher.title")}>
      <NavDropdown.Item as={Link} to={originalPath} language="ro">
        Romn
      </NavDropdown.Item>
      <NavDropdown.Item as={Link} to={originalPath} language="en">
        English
      </NavDropdown.Item>
    </NavDropdown>
  );
};

export default LanguageSwitcher;
