import React from "react";
import { Link, useTranslation } from "gatsby-plugin-react-i18next";
import LanguageSwitcher from "./language-switcher";

import { IconType } from "react-icons";
import { FaFacebook, FaInstagram, FaLinkedin } from "react-icons/fa";
import { HiOutlineMail } from "react-icons/hi";

import * as styles from "./footer.module.css";

type ContactProps = {
  link: string;
  icon: IconType;
};

const Contact: React.FC<ContactProps> = ({ link, icon: Icon }) => (
  <a href={link} target={"_blank"} rel={"noreferrer"} className={"link"}>
    <Icon size={20} />
  </a>
);

const Footer: React.FC = () => {
  const { t } = useTranslation();

  return (
    <div className={styles.contact}>
      <div className={styles.socialMedia}>
        <Contact
          icon={FaFacebook}
          link={"https://www.facebook.com/nitro.nlp"}
        />
        <Contact
          icon={FaInstagram}
          link={"https://www.instagram.com/nitro_nlp/"}
        />
        <Contact
          icon={FaLinkedin}
          link={"https://www.linkedin.com/company/nitro-nlp/"}
        />
        <Contact icon={HiOutlineMail} link={"mailto:contact@nitronlp.rocks"} />
      </div>

      <div className={styles.socialMedia}>
        <Link to={"/rules"} className={"link"}>
          {t("footer.rules")}
        </Link>
        <Link to={"/archive"} className={"link"}>
          {t("footer.archive")}
        </Link>
        <Link to={"/team"} className={"link"}>
          {t("footer.team")}
        </Link>
        <LanguageSwitcher className={"link"} />
      </div>
    </div>
  );
};

export default Footer;
