import React from "react";
import { Link, useTranslation } from "gatsby-plugin-react-i18next";
import { Button } from "react-bootstrap";
import useEditionData from "../../hooks/useEditionData";
import * as styles from "../../pages/index.module.css";

type PeriodType = {
  props: any;
  text: string;
};

const preRegistration: PeriodType = {
  props: {
    to: "/schedule",
  },
  text: "navbar.schedule",
};
const Registration: PeriodType = {
  props: {
    as: "a",
    href: "https://forms.zohopublic.eu/nitrolanguageprocessing/form/nscriereNitroHackathon/formperma/pHkcA9Uoak67yDFN8favZvt4aN1PTQSw1mkKYIsnATc",
  },
  text: "register",
};
const postRegistration: PeriodType = {
  props: {
    as: "a",
    href: useEditionData(2024).leaderboard,
  },
  text: "navbar.leaderboard",
};

type RegisterButtonProps = {
  className?: string;
  inNavbar?: boolean;
};

const RegisterButton: React.FC<RegisterButtonProps> = ({ inNavbar }) => {
  const { t } = useTranslation();
  const registrationIntervals =
    useEditionData(2024).hackathonRegistration.split("-");
  const registrationDates = [
    new Date(`${registrationIntervals[0]} 2024`),
    new Date(`${registrationIntervals[1]} 2024`),
  ];
  const today = new Date();
  const period =
    today < registrationDates[0]
      ? preRegistration
      : today < registrationDates[1]
        ? Registration
        : postRegistration;

  return (
    !(period == preRegistration && inNavbar) && (
      <Button className={inNavbar ? "" : `button ${styles.button}`}>
        {period == preRegistration && (
          <Link {...period.props} className={"buttonLink"}>
            {t(period.text)}
          </Link>
        )}
        {period != preRegistration && (
          <a {...period.props} className={"buttonLink"}>
            {t(period.text)}
          </a>
        )}
      </Button>
    )
  );
};

export default RegisterButton;
