import { useMemo } from "react";

import { graphql, useStaticQuery } from "gatsby";

import { useI18next } from "gatsby-plugin-react-i18next";

import useSiteMetadata from "../hooks/useSiteMetadata";
import React from "react";

type GatsbyImageData = {
  images: {
    fallback: {
      src: string;
    };
  };
  width: number;
  height: number;
};

type OpenGraphImageQuery = {
  ogImage?: {
    childImageSharp?: {
      gatsbyImageData: GatsbyImageData;
    };
  };
};

type SeoProps = {
  title?: string;
  description?: string;
  keywords?: string[];
  pathname: string;
};

const Seo: React.FC<SeoProps> = (props) => {
  const { title: defaultTitle, siteUrl } = useSiteMetadata();

  const keywords = useMemo(() => {
    if (!props.keywords) {
      return;
    }

    return props.keywords
      .map((keyword) => keyword.trim())
      .filter((keyword) => !!keyword)
      .join(",");
  }, [props.keywords]);

  const canonicalUrl = siteUrl + props.pathname;

  const { language } = useI18next();

  const locale = useMemo(() => {
    switch (language) {
      case "en":
        return "en_US";
      case "ro":
        return "ro_RO";
      default:
        throw `unsupported language code: ${language}`;
    }
  }, [language]);

  const { ogImage } = useStaticQuery<OpenGraphImageQuery>(graphql`
    query {
      ogImage: file(
        sourceInstanceName: { eq: "images" }
        relativePath: { eq: "social-media-preview-image.png" }
      ) {
        childImageSharp {
          gatsbyImageData(layout: FIXED, width: 1200)
        }
      }
    }
  `);
  const ogImageData = ogImage?.childImageSharp?.gatsbyImageData;

  const ogImageUrl = ogImageData?.images?.fallback?.src;
  const ogImageWidth = ogImageData?.width;
  const ogImageHeight = ogImageData?.height;

  return (
    <>
      <title>{props.title || defaultTitle}</title>
      {props.description && (
        <meta name="description" content={props.description} />
      )}
      {keywords && <meta name="keywords" content={keywords} />}
      <meta property="og:title" content="Nitro NLP Hackathon" />
      <meta property="og:type" content="website" />
      <meta property="og:url" content={canonicalUrl} />
      <meta property="og:locale" content={locale} />
      {ogImageUrl && (
        <meta property="og:image" content={`${siteUrl}${ogImageUrl}`} />
      )}
      {ogImageWidth && (
        <meta property="og:image:width" content={ogImageWidth.toString()} />
      )}
      {ogImageHeight && (
        <meta property="og:image:height" content={ogImageHeight.toString()} />
      )}
    </>
  );
};

export default Seo;
